/* Вся секция */
.story-page {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  color: #000000;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

/* Внутренний блок, узкая колонка */
.story-inner {
  max-width: 520px;
  width: 100%;
  text-align: center;

  opacity: 0;
  transform: translateY(30px);
  transition: all 1s cubic-bezier(0.16, 1, 0.3, 1);
}

.story-visible {
  opacity: 1;
  transform: translateY(0);
}

/* --- верхняя эмблема (инициалы в венке) --- */

.crest-block {
  margin-bottom: 24px;
}

.crest-circle {
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  position: relative;
}

.crest-svg {
  width: 130px;
  height: 130px;
  display: block;
}

.crest-initials {
  position: absolute;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.2rem;
  line-height: 1;
  font-weight: 400;
  letter-spacing: 0;
  color: #000;
  top: 50%;
  left: 50%;
  translate: -50% -50%;
  white-space: nowrap;
}

/* --- основной текст-приглашение --- */

.invitation-text {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: 1.3rem;
  font-weight: 400;
  line-height: 1.6;
  color: #000;
  margin: 0 auto 32px;
  max-width: 480px;
  /* делаем его как будто рукописное обращение */
}

/* блок с кольцами */
.rings-block {
  margin-bottom: 28px;
}

.rings-svg {
  width: 80px;
  max-width: 90px;
  height: auto;
  stroke: #000;
  fill: none;
  display: inline-block;
}

/* инфо: имена, дата, место */
.who-when {
  margin-bottom: 32px;
  color: #000;
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  line-height: 1.5;
}

.couple-names {
  font-size: 1.4rem;
  font-weight: 400;
  margin-bottom: 8px;
}

.date-line {
  font-size: 1.1rem;
  font-weight: 400;
  margin-bottom: 6px;
}

.place-line {
  font-size: 1.1rem;
  font-weight: 400;
}

/* маленький декоративный разделитель */
.divider-floral {
  margin: 24px 0 28px;
}

.floral-svg {
  width: 90px;
  height: auto;
  stroke: #000;
  display: inline-block;
}

/* нижняя подпись "С уважением..." */
.hosts-block {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  color: #000;
  line-height: 1.5;
}

.hosts-label {
  font-size: 1.2rem;
  margin-bottom: 6px;
  font-weight: 400;
}

.hosts-names {
  font-size: 1.3rem;
  font-weight: 400;
}

/* адаптив */

@media (max-width: 480px) {
  .story-page {
    padding: 60px 20px 80px;
  }

  .crest-svg {
    width: 60px;
    height: 60px;
  }

  .crest-initials {
    font-size: 1rem;
  }

  .invitation-text {
    font-size: 1.15rem;
    line-height: 1.5;
    margin-bottom: 28px;
  }

  .rings-svg {
    width: 70px;
  }

  .couple-names {
    font-size: 1.3rem;
  }

  .date-line,
  .place-line {
    font-size: 1rem;
  }

  .hosts-label {
    font-size: 1.1rem;
  }

  .hosts-names {
    font-size: 1.2rem;
  }
}
