/* Секция целиком */
.program-simple {
  width: 100%;
  background: #ffffff;
  color: #000;
  display: flex;
  justify-content: center;
  padding: 80px 20px 100px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.program-inner {
  max-width: 800px;
  width: 100%;
  text-align: center;
  position: relative;
}

/* Заголовок сверху */
.program-top-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-size: clamp(2rem, 1rem + 2vw, 2.4rem);
  font-weight: 400;
  line-height: 1.2;
  color: #000;
  margin: 0 0 40px;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
}

/* Обёртка таймлайна */
.timeline-wrapper {
  position: relative;
  width: 100%;
  /* слегка прижмём контент к центру чтобы выглядело как в примере */
  padding: 0 20px;
}

/* Вертикальная линия посередине */
.timeline-line {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 2px;
  background-color: #000;
  transform: translateX(-50%);
}

/* Один пункт программы (горизонтальная строка) */
.timeline-item {
  position: relative;
  display: grid;
  grid-template-columns: 1fr auto 1fr; /* слева | центр | справа */
  align-items: center;
  text-align: left;
  margin-bottom: 40px;
  font-family: "Playfair Display", Georgia, serif;
  color: #000;
  line-height: 1.4;
}

/* Колонка слева и справа */
.timeline-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 50px;
  padding: 0 10px;
  font-size: 1rem;
  color: #000;
}

/* Выравнивание текста */
.timeline-col-left {
  text-align: right;
  align-items: flex-end;
}
.timeline-col-right {
  text-align: left;
  align-items: flex-start;
}

/* Время (больше размером) */
.timeline-col-time {
  font-size: 1.5rem;
  font-weight: 400;
  margin-bottom: 6px;
  font-family: "Playfair Display", Georgia, serif;
  color: #000;
}

/* Текст под временем */
.timeline-col-text {
  font-size: 1rem;
  font-weight: 400;
  color: #000;
  max-width: 200px;
  line-height: 1.4;
  text-wrap: balance;
}

/* Центральная точка */
.timeline-marker {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 24px;
  min-height: 24px;
}

.marker-dot {
  width: 10px;
  height: 10px;
  background-color: #000;
  border-radius: 50%;
  position: relative;
  z-index: 2;
}

/* Небольшой лайфхак:
   линия на фоне идёт через центр.
   Мы не рисуем отдельный отрезок здесь, только точку поверх линии. */

/* Адаптив для планшетов */
@media (max-width: 768px) {
  .program-top-title {
    font-size: 2rem;
    margin-bottom: 32px;
  }

  .timeline-col-time {
    font-size: 1.3rem;
  }

  .timeline-col-text {
    font-size: 0.95rem;
    max-width: 220px;
  }

  .timeline-item {
    margin-bottom: 32px;
  }
}

/* Мобильный:
   на очень узком экране обычно Tilda складывает всё в одну колонку вниз.
   Мы сделаем: время сверху жирно, текст под ним,
   точка и линия остаются слева как "ось".
*/
@media (max-width: 500px) {
  .timeline-wrapper {
    padding-left: 10px;
  }

  .timeline-line {
    left: 20px;
    transform: none;
  }

  .timeline-item {
    grid-template-columns: 40px 1fr;
    grid-template-areas:
      "marker left"
      "marker right";
    row-gap: 12px;
    text-align: left;
  }

  .timeline-col-left,
  .timeline-col-right {
    align-items: flex-start;
    text-align: left;
    padding-left: 10px;
  }

  .timeline-col-left {
    grid-area: left;
  }

  .timeline-col-right {
    grid-area: right;
  }

  .timeline-marker {
    grid-area: marker;
    align-items: flex-start;
    justify-content: center;
    min-width: 24px;
    min-height: 100%;
  }

  .timeline-col-time {
    font-size: 1.2rem;
    margin-bottom: 4px;
  }

  .timeline-col-text {
    font-size: 0.9rem;
    line-height: 1.4;
    max-width: 100%;
  }
}
