/* сцена */
.home {
  background: #1f1e1c;
  color: #ffffff;
  font-family: "Poppins", system-ui, -apple-system, BlinkMacSystemFont,
    "Segoe UI", Roboto, "Helvetica Neue", sans-serif;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  scroll-snap-align: none;
}

/* обёртка героя */
.hero {
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-direction: column;
}

/* блок с фотографией */
.hero-image-wrapper {
  position: relative;
  width: 100%;
  aspect-ratio: 9 / 13; /* элегантный вертикальный формат как журнальная обложка */
  max-height: 80vh;
  overflow: hidden;
  border-radius: 24px;
  box-shadow: 0 40px 120px rgba(0, 0, 0, 0.7);
  margin: 40px auto 32px;
  isolation: isolate;
}

/* само фото */
.hero-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
  filter: brightness(0.9) contrast(1.05) saturation(1.05);
  transform: scale(1.03);
  animation: photoReveal 1.5s ease forwards;
}

/* мягкая виньетка сверху и снизу */
.hero-overlay-top {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.5) 0%,
    rgba(0, 0, 0, 0) 40%
  );
  pointer-events: none;
  opacity: 0;
  animation: overlayFade 1.5s ease forwards 0.2s;
}

.hero-overlay-bottom {
  content: "";
  position: absolute;
  inset: 0;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.45) 0%,
    rgba(0, 0, 0, 0) 50%
  );
  pointer-events: none;
  opacity: 0;
  animation: overlayFade 1.5s ease forwards 0.2s;
}

/* текст по центру фото */
.hero-text {
  position: absolute;
  top: 14%;
  width: 100%;
  text-align: center;
  padding: 0 16px;
  transform: translateY(-20px);
  opacity: 0;
  animation: textRise 1.9s cubic-bezier(0.16, 1, 0.3, 1) forwards 0.4s;
}

.hero-names {
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  font-weight: 400;
  font-size: clamp(2rem, 1rem + 2vw, 3rem);
  line-height: 1.2;
  letter-spacing: 0.08em;
  text-transform: none;
  color: #fff;
  background: radial-gradient(
    circle at 30% 30%,
    #ffffff 0%,
    #ffffff 40%,
    rgba(255, 255, 255, 0.5) 60%,
    rgba(255, 255, 255, 0.15) 80%
  );
  -webkit-background-clip: text;
  color: transparent;
  text-shadow: 0 20px 40px rgba(0, 0, 0, 0.9);
  font-feature-settings: "liga", "kern";
  position: relative;
  
}

.hero-names::after {
  content: "";
  display: block;
  width: 80px;
  height: 1px;
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(255, 255, 255, 0.6) 50%,
    rgba(255, 255, 255, 0) 100%
  );
  margin: 16px auto 0;
  box-shadow: 0 0 10px rgba(255,255,255,.8);
}
*

/* индикатор "скролли дальше" внизу фото */
.scroll-indicator {
  position: absolute;
  left: 50%;
  bottom: 20px;
  transform: translateX(-50%);
  text-align: center;
  font-size: 0.7rem;
  font-weight: 300;
  letter-spacing: 0.2em;
  color: rgba(255, 255, 255, 0.7);
  font-family: "Poppins", sans-serif;
  text-transform: uppercase;
  opacity: 0;
  animation: fadeIn 2s ease forwards 1.2s;
  user-select: none;
}

.scroll-indicator .dot {
  display: block;
  width: 6px;
  height: 6px;
  background: #ffffff;
  border-radius: 50%;
  margin: 0 auto 6px;
  box-shadow: 0 0 12px rgba(255, 255, 255, 0.9);
  animation: dotPulse 2s infinite;
}

.scroll-indicator .line {
  display: block;
  width: 2px;
  height: 36px;
  margin: 0 auto 8px;
  background: rgba(255, 255, 255, 0.8);
  border-radius: 2px;
  animation: lineFloat 2s infinite;
}

.scroll-text {
  display: block;
  opacity: 0.8;
}

/* подпись под фото */
.below-hero {
  width: 100%;
  display: flex;
  justify-content: center;
  padding: 24px 16px 60px;
  text-align: center;
}

.tagline {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Playfair Display", Georgia, serif;
  color: #ffffff;
}

.tag-main {
  font-size: clamp(1rem, 0.5rem + 1vw, 1.2rem);
  font-weight: 400;
  line-height: 1.3;
  letter-spacing: 0.15em;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.75);
  margin-bottom: 6px;
}

.tag-date {
  font-size: clamp(1.2rem, 0.8rem + 1vw, 1.5rem);
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0.08em;
  color: #fff;
  background: radial-gradient(
    circle at 30% 30%,
    #ffffff 0%,
    rgba(255, 255, 255, 0.8) 40%,
    rgba(255, 255, 255, 0.25) 70%
  );
  -webkit-background-clip: text;
  color: transparent;
  text-shadow: 0 30px 60px rgba(0, 0, 0, 0.9);
}

/* анимации */
@keyframes photoReveal {
  0% {
    opacity: 0;
    transform: scale(1.07) translateY(20px);
  }
  100% {
    opacity: 1;
    transform: scale(1.03) translateY(0);
  }
}

@keyframes overlayFade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes textRise {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  60% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    transform: translateY(0);
  }
}

@keyframes dotPulse {
  0%,
  100% {
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.6),
      0 0 20px rgba(255, 255, 255, 0.25);
  }
  50% {
    box-shadow: 0 0 12px rgba(255, 255, 255, 1),
      0 0 30px rgba(255, 255, 255, 0.4);
  }
}

@keyframes lineFloat {
  0%,
  100% {
    transform: translateY(0px);
    opacity: 0.8;
  }
  50% {
    transform: translateY(6px);
    opacity: 0.4;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    filter: blur(4px);
  }
  to {
    opacity: 1;
    filter: blur(0);
  }
}

/* адаптив — чтобы на мобиле не было странной узкой колонки даты */
@media (min-width: 480px) {
  .tagline {
    flex-direction: row;
    gap: 12px;
  }

  .tag-main {
    margin-bottom: 0;
  }

  .tag-date {
    letter-spacing: 0.12em;
  }
}
