/* Вся секция таймера — белый фон страницы */
.countdown-page {
  background: #ffffff;
  width: 100%;
  min-height: 100vh;
  padding: 60px 20px 100px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
  color: #fff;
}

/* Внутренняя обёртка */
.countdown-inner {
  width: 100%;
  max-width: 900px;
  display: flex;
  justify-content: center;
}

/* Карточка с фото (как на скрине — не до краёв экрана) */
.photo-card {
  position: relative;
  width: 100%;
  max-width: 700px;
  border: 1px solid #dcdcdc; /* чтобы фото выглядело как вставленное на белом */
  background: #000; /* на случай, если фото не загрузится */
  overflow: hidden;
  /* верх и бока прямые, низ будет "рваный" через псевдоэлемент */
}

/* Фото */
.photo-img {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  object-position: center;
  filter: brightness(0.9) contrast(1.05);
}

/* Затемнение в центре фото, чтобы текст читался */
.photo-gradient {
  position: absolute;
  inset: 0;
  background: radial-gradient(
    circle at 50% 60%,
    rgba(0, 0, 0, 0.4) 0%,
    rgba(0, 0, 0, 0.0) 60%
  );
  pointer-events: none;
}

/* Контейнер текста поверх фото */
.photo-overlay {
  position: absolute;
  left: 50%;
  top: 55%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #fff;
  text-shadow: 0 4px 12px rgba(0, 0, 0, 0.6);
  width: 90%;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Заголовок "До события осталось" */
.until-title {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-weight: 400;
  line-height: 1.2;
  font-size: clamp(1.8rem, 1rem + 1vw, 2.4rem);
  margin: 0 0 24px;
  color: #fff;
  letter-spacing: 0.03em;
}

/* Ряд таймера */
.timer-row {
  display: flex;
  gap: 28px;
  flex-wrap: wrap;
  justify-content: center;
}

/* Колонка значения */
.timer-col {
  min-width: 70px;
  text-align: center;
  color: #fff;
  font-family: "Playfair Display", Georgia, serif;
  line-height: 1.3;
}

/* Большое число */
.timer-value {
  font-size: clamp(2rem, 1rem + 1vw, 2.6rem);
  font-weight: 400;
  color: #fff;
  min-width: 2ch;
  transition: transform 0.4s;
}

/* Подпись (дней / часов / минут / секунд) */
.timer-label {
  font-size: 0.8rem;
  font-weight: 400;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  color: #fff;
  opacity: 0.9;
  margin-top: 8px;
  font-family: "Poppins", system-ui, sans-serif;
  text-shadow: 0 4px 12px rgba(0,0,0,.6);
}

/* Маленькая анимация "переворота" если число сменилось */
.flip {
  animation: flipOnce 0.6s ease;
}

@keyframes flipOnce {
  0%   { transform: rotateX(0deg); }
  50%  { transform: rotateX(180deg); }
  100% { transform: rotateX(360deg); }
}

/* Рваный край снизу */
.torn-edge {
  position: absolute;
  left: 0;
  right: 0;
  bottom: -1px;
  height: 60px;
  line-height: 0;
  overflow: hidden;
  pointer-events: none;
}

.torn-svg {
  display: block;
  width: 100%;
  height: 60px;
}

.torn-svg path {
  fill: #ffffff; /* тот же белый фон страницы */
}

/* адаптив */
@media (max-width: 600px) {
  .countdown-page {
    padding-top: 40px;
  }

  .photo-overlay {
    top: 55%;
    width: 92%;
  }

  .until-title {
    font-size: 1.8rem;
    margin-bottom: 20px;
  }

  .timer-row {
    gap: 20px;
  }

  .timer-value {
    font-size: 2rem;
  }

  .timer-label {
    font-size: 0.7rem;
    margin-top: 6px;
  }
}
