/* страница: белый фон, ч/б типографика */
.address-page {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  color: #000;
  display: flex;
  justify-content: center;
  padding: 80px 20px 120px;
  font-family: "Playfair Display", Georgia, "Times New Roman", serif;
}

.address-inner {
  width: 100%;
  max-width: 900px;
  text-align: center;
}

/* заголовок каллиграфией (как на скрине) */
.address-heading {
  font-family: "Great Vibes", "Playfair Display", Georgia, serif;
  font-weight: 400;
  font-size: clamp(2rem, 1rem + 2vw, 2.6rem);
  line-height: 1.2;
  margin: 0 0 10px;
}

/* иконка колец */
.rings-icon {
  width: 42px;
  height: 42px;
  margin: 6px auto 22px;
}
.rings-icon svg {
  width: 100%;
  height: 100%;
}

/* текст адреса */
.venue-name {
  font-size: 1.15rem;
  margin: 0 0 6px;
}
.venue-address {
  font-size: 1.05rem;
  color: #111;
  margin: 0 0 26px;
}

/* карта в «вклеенной карточке» */
.map-wrap {
  position: relative;
  margin: 0 auto;
  max-width: 820px;
  border: 1px solid #dedede;
  box-shadow: 0 18px 40px rgba(0,0,0,0.08);
  overflow: hidden;
  background: #f7f7f7;
}
.dg-map {
  width: 100%;
  height: 380px;
}
.torn-bottom {
  position: absolute;
  left: 0; right: 0; bottom: -1px;
  height: 60px; line-height: 0;
}
.torn-bottom svg { width: 100%; height: 60px; display: block; }

/* кнопки действий */
.actions {
  display: flex;
  gap: 12px;
  justify-content: center;
  margin-top: 18px;
}
.btn {
  font-family: "Poppins", system-ui, sans-serif;
  font-size: 0.95rem;
  padding: 10px 16px;
  border-radius: 999px;
  background: #000;
  color: #fff;
  text-decoration: none;
  border: 1px solid #000;
  transition: transform .2s ease, opacity .2s ease;
}
.btn:hover { transform: translateY(-2px); }
.btn:active { transform: translateY(0); opacity: .9; }

.btn-outline {
  background: #fff;
  color: #000;
  border-color: #000;
}

/* сообщение об ошибке */
.map-error {
  margin-top: 12px;
  font-size: 0.95rem;
  color: #a00;
}

/* адаптив */
@media (max-width: 600px) {
  .address-page { padding: 60px 16px 80px; }
  .dg-map { height: 320px; }
}
